import React,{useEffect} from 'react';
import { makeStyles, createStyles, Theme } from '@material-ui/core/styles';
import ClickAwayListener from '@material-ui/core/ClickAwayListener';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import Divider from "@material-ui/core/Divider";
import Button from '@material-ui/core/Button';
import Avatar from '@material-ui/core/Avatar';
import InputBase from '@material-ui/core/InputBase';
import IconButton from "@material-ui/core/IconButton";
import SearchIcon from "@material-ui/icons/Search";
import axios from 'axios'
import Typography from '@material-ui/core/Typography';
import {Routes} from '../../Routes'
import { Link } from "react-router-dom";
import useDebounce from '../useDebounce';
const mangaListStyles = makeStyles(theme => ({
    root: {
        padding:0,
        paddingTop:8,
        paddingBottom:8,
        width:392
    },
    item:{
        height: 80,
        padding:'0px 8px'
    },
    text:{
      paddingTop:theme.spacing(1),
    },
    itemImg:{
        height: 60,
        marginRight:theme.spacing(1),
        marginLeft:theme.spacing(0.5)
    },
}));

const ButtonLink = (props) => <Button {...props} component={Link}></Button>

const MangaList = ({sub}) => {
  const classes = mangaListStyles();
  const [mangaList, setMangaList] = React.useState([]);
  const [isLoading, setIsLoading] = React.useState(true);
  const fetchMangaList =async(sub)=>{
    setIsLoading(true)
    try {
      // console.log('search')
      const response = await axios.get(`${process.env.REACT_APP_API_BASE}/manga/search?sub=${sub}`)
      // console.log('üëâ Returned data:', response.data);
      setMangaList(response.data.list)
    } catch (e) {
      setMangaList([])
      console.log(`üò± Axios request failed: ${e}`);
    }
    setIsLoading(false)
  }
  useEffect(()=>{
    fetchMangaList(sub)
  },[sub])
  if(!mangaList.length&&!isLoading){
    return <div style={{padding:16}}>
      <Typography variant="subtitle1" component="h6"  className={classes.subTitle}>
        –ù–∏—á–µ–≥–æ –Ω–µ –Ω–∞–π–¥–µ–Ω–æ
      </Typography>
    </div>
  }
  return(
    <div>
      <List className={classes.root}>
        {
            mangaList.map(item=>(
                <ListItem key={item.id}  to={Routes.Manga.link(item.id)} component={ButtonLink} alignItems="flex-start" className={classes.item}>
                <ListItemAvatar>
                    <img src={item.imgUrl} className={classes.itemImg}></img>
                </ListItemAvatar>
                <ListItemText
                className={classes.text}
                primary={item.title}
                secondary={item.englishTitle}
                />
                </ListItem>
            ))
        }
      </List>
    </div>
  )}






  const userListStyles = makeStyles(theme => ({
    root: {
        padding:0,
        paddingTop:8,
        paddingBottom:8,
        width:392
    },
    item:{
        height: 64,
        padding:'0px 8px'
    },

    itemImg:{
        height: 48,
        marginRight:theme.spacing(1),
        marginLeft:theme.spacing(0.5)
    },
}));


const UserList = ({sub}) => {
  const classes = userListStyles();
  const [userList, setUserList] = React.useState([]);
  const [isLoading, setIsLoading] = React.useState(true);
  const fetchUserList =async(sub)=>{
    setIsLoading(true)
    try {
      // console.log('search')
      const response = await axios.get(`${process.env.REACT_APP_API_BASE}/user/search?sub=${sub}`)
      // console.log('üëâ Returned data:', response.data);
      setUserList(response.data.list)
    } catch (e) {
      setUserList([])
      console.log(`üò± Axios request failed: ${e}`);
    }
    setIsLoading(false)
  }
  useEffect(()=>{
    fetchUserList(sub)
  },[sub])
  if(!userList.length&&!isLoading){
    return <div style={{padding:16}}>
      <Typography variant="subtitle1" component="h6"  className={classes.subTitle}>
        –ù–∏—á–µ–≥–æ –Ω–µ –Ω–∞–π–¥–µ–Ω–æ
      </Typography>
    </div>
  }
  return(
    <div>
      <List className={classes.root}>
        {
            userList.map(item=>(
                <ListItem key={item.id} to={Routes.User.link(item.id)} component={ButtonLink} alignItems="flex-start" className={classes.item}>
                <ListItemAvatar>
                  <Avatar alt={item.username} src={item.imgUrl} />
                </ListItemAvatar>
                <ListItemText
                className={classes.text}
                primary={item.username}
                secondary={item.isOnline?'Online':'offline'}
                />
                </ListItem>
            ))
        }
      </List>
    </div>
  )}





  const useStyles = makeStyles((theme) =>
  createStyles({
    root: {
      position: 'relative',
    },
    dropdown: {
      display:'flex',
      flexDirection: 'column',
      alignItems: 'center',
      position: 'absolute',
      width:400,
      top: 48,
      right: 0,
      // left: 0,
      zIndex: 1,
      // border: '1px solid',
      boxShadow: theme.overrides.MuiPaper.boxShadow,
      backgroundColor: theme.palette.background.paper,
    },
    tabs:{
      width:'100%',
    },
    divider:{
      width:'100%',
    },
  }),
);

export default function ClickAway() {
  const classes = useStyles();
  const [open, setOpen] = React.useState(false);
  const [value, setValue] = React.useState(0);
  const [subStr, setSubStr] = React.useState("");
  const [debStr, setDebStr] = React.useState("");
  const debouncedSubStr = useDebounce(subStr, 100);
  useEffect(
    () => {
      setDebStr(debouncedSubStr)
    },
    [debouncedSubStr]
  );
  const handleClick = () => {
    setOpen(true);
  };

  const handleClickAway = () => {
    setSubStr("")
    setOpen(false);
  };
 
  return (
    <ClickAwayListener onClickAway={handleClickAway}>
      <div className={classes.root}>
        {/* <button type="button" onClick={handleClick}>
          Open menu dropdown
        </button> */}
        <div style={{ padding: "2px 8px", display: "flex", alignItems: "center"}}>
          <InputBase value={subStr} onChange={e=>setSubStr(e.target.value)} onFocus={handleClick} placeholder="–ü–æ–∏—Å–∫" />
          <IconButton size="small">
              <SearchIcon />
          </IconButton>
        </div>
        {open ? (
          <div className={`${classes.dropdown} search`}>
                <Tabs
                    centered
                    className={classes.tabs}
                    value={value}
                    indicatorColor="primary"
                    textColor="primary"
                    onChange={(e,v)=>setValue(v)}
                    aria-label="disabled tabs example"
                >
                    <Tab className={classes.tab} label={"–º–∞–Ω–≥–∞"} />
                    <Tab className={classes.tab} label={"–ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å"} />
                </Tabs>
                <Divider className={classes.divider} />
                {
                  debStr.trim().length>=3
                  ?<div>
                    {value
                    ?<UserList sub={debStr}/>
                    :<MangaList sub={debStr}/>
                    }
                  </div>
                  :<div style={{padding:16}}>
                      <Typography variant="subtitle1" component="h6"  className={classes.subTitle}>
                        –î–ª—è –ø–æ–∏—Å–∫–∞ –≤–≤–µ–¥–∏—Ç–µ –Ω–µ –º–µ–Ω–µ–µ —Ç—Ä–µ—Ö —Å–∏–º–≤–æ–ª–æ–≤
                      </Typography>
                  </div>
                }
          </div>
        ) : null}
      </div>
    </ClickAwayListener>
  );
}
