import React,{useEffect} from 'react';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import ClickAwayListener from '@material-ui/core/ClickAwayListener';
import Grow from '@material-ui/core/Grow';
import Paper from '@material-ui/core/Paper';
import Popper from '@material-ui/core/Popper';
import MenuItem from '@material-ui/core/MenuItem';
import MenuList from '@material-ui/core/MenuList';
import { makeStyles } from '@material-ui/core/styles';

//box-shadow: 0 8px 20px 0 rgba(218,224,235,.6);
import Divider from "@material-ui/core/Divider";
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import { Link,Route,useLocation ,useHistory} from "react-router-dom";
import { TabRouteList,Routes } from '../../Routes'
import { observer, inject} from 'mobx-react';
import Search from './search'
import Drawer from '@material-ui/core/Drawer';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import IconButton from '@material-ui/core/IconButton';
import ListItemText from '@material-ui/core/ListItemText';
import MenuIcon from '@material-ui/icons/Menu';
import Hidden from '@material-ui/core/Hidden';
const useStyles = makeStyles(theme => ({
    root:{
        backgroundColor:theme.palette.background.paper,
        // boxShadow: theme.shadows.elevation1,
    },
    toolBar:{
        display: "flex",
        justifyContent: "space-between",
        alignItems: "center",
        paddingLeft:theme.spacing(1.5),
        paddingRight:theme.spacing(1.5),
        minHeight: 48,
        // boxShadow: "0 8px 20px 0 rgba(218,224,235,.6)",
    },
    divider: {
      height: 30,
      margin: theme.spacing(1),
    },
    button:{
        margin: theme.spacing(0.5),
    },
    logo:{
        borderRadius: 0,
        margin: theme.spacing(0.5),
    },
    tabs:{
        marginLeft: theme.spacing(1.5),
    },
    indicator:{
        opacity: 0
    },
    selected:{
        color: 'rgba(0, 0, 0, 0.54) !important'
    },
    tab:{
        minWidth:0
    }
}));







const NavBar = observer(({authStore}) => {
  const [value, setValue] = React.useState(2);
  const [isActive, setIsActive] = React.useState(false);

  const classes = useStyles(isActive);
  const setTab = (v) => {
     
      setValue(v)
  }
  const { pathname } = useLocation();
  useEffect(()=>{
    // console.log(pathname=='/','/')
    let v = null;
    TabRouteList.map((route,i)=>{
        // console.log(route.path,pathname,route.path==pathname)
        if(route.path==pathname){
            v=i
            setIsActive(true)
            setValue(v)
        }
    })
    if(v===null){
        setIsActive(false)
    }
  },[pathname])
//   matchPath("/users", { path: "/users/:id", exact: true, strict: true});
  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
        <AppBar elevation={3} className={classes.root}>
        <Toolbar className={classes.toolBar}>
        <Hidden mdUp>
            <TemporaryDrawer/>   
        </Hidden>
        <Hidden smDown>
            <div style={{ display: "flex", alignItems: "center"}}>
                <Avatar component={Link} to={'/'} src="/logo192.png" className={classes.logo}/>
                <Tabs
                    classes={isActive?{root:classes.tabs}:{root:classes.tabs,indicator:classes.indicator}}
                    value={value}
                    indicatorColor="primary"
                    textColor="primary"
                    // onChange={handleChange}
                    aria-label="disabled tabs example"
                >
                    {TabRouteList.map(route=><Tab classes={isActive||{selected:classes.selected}} key={route.name} component={Link} to={route.path} className={classes.tab} label={route.ruName} />)}
                </Tabs>
            </div>
            </Hidden>
            
            <div style={{display: "flex", alignItems: "center" }}>
                <Search/>
                
                <Divider orientation="vertical" className={classes.divider} />
                {
                    authStore.current_user===null
                    ?<>
                        <Button component={Link} to={Routes.SignIn.path} size="small" className={classes.button}>
                            –í–æ–π—Ç–∏
                        </Button>
                        <Button component={Link} to={Routes.SignUp.path} variant="outlined" size="small" color="secondary" className={classes.button}>
                            –†–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—è
                        </Button>
                    </>
                    :<>
                        {/* <Button component={Link} to={Routes.User.link(authStore.current_user.id)} size="small" className={classes.button}>
                        {authStore.current_user.username}
                        </Button> */}
                        <UserButton user={authStore.current_user} logout={authStore.logout}/>
                    </>
                }
                
            </div>
            </Toolbar>
        </AppBar>
  );
})
const useStylesButton = makeStyles((theme) => ({
    root: {
        display: 'flex',
    },
    paper: {
        marginRight: theme.spacing(2),
    },
}));
function UserButton({user,logout}){
    const history = useHistory();
    const classes = useStylesButton();
    const [open, setOpen] = React.useState(false);
    const anchorRef = React.useRef(null);
  
    const handleToggle = () => {
      setOpen((prevOpen) => !prevOpen);
    };
  
    const handleClose = (event) => {
      if (anchorRef.current && anchorRef.current.contains(event.target)) {
        return;
      }
  
      setOpen(false);
    };
  
    function handleListKeyDown(event) {
      if (event.key === 'Tab') {
        event.preventDefault();
        setOpen(false);
      }
    }
  
    // return focus to the button when we transitioned from !open -> open
    const prevOpen = React.useRef(open);
    React.useEffect(() => {
      if (prevOpen.current === true && open === false) {
        anchorRef.current.focus();
      }
  
      prevOpen.current = open;
    }, [open]);
  
    return (
      <div className={classes.root}>
        <div>
          <Button
            ref={anchorRef}
            aria-controls={open ? 'menu-list-grow' : undefined}
            aria-haspopup="true"
            onClick={handleToggle}
          >
            {user.username}
          </Button>
          <Popper open={open} anchorEl={anchorRef.current} role={undefined} transition disablePortal>
            {({ TransitionProps, placement }) => (
              <Grow
                {...TransitionProps}
                style={{ transformOrigin: placement === 'bottom' ? 'center top' : 'center bottom' }}
              >
                <Paper>
                  <ClickAwayListener onClickAway={handleClose}>
                    <MenuList autoFocusItem={open} id="menu-list-grow" onKeyDown={handleListKeyDown}>
                      <MenuItem component={Link} to={Routes.User.link(user.id)} onClick={handleClose}>–ü—Ä–æ—Ñ–∏–ª—å</MenuItem>
                      {
                          Routes.User.tabList.map(item => <MenuItem id={item.subLink} component={Link} to={Routes.User.link(user.id)+'/'+item.subLink}
                           onClick={handleClose}>{item.label}</MenuItem>)
                      }
                      <MenuItem  component={Link} to={Routes.EditProfilePage.path} onClick={handleClose}>–ù–∞—Å—Ç—Ä–æ–π–∫–∏</MenuItem>
                      <MenuItem  component={Link} to={Routes.SignIn.path} onClick={logout}>–í—ã—Ö–æ–¥</MenuItem>
                    </MenuList>
                  </ClickAwayListener>
                </Paper>
              </Grow>
            )}
          </Popper>
        </div>
      </div>
    );
}




function TemporaryDrawer() {

  const [state, setState] = React.useState({
    top: false,
    left: false,
    bottom: false,
    right: false,
  });

  const toggleDrawer = (anchor, open) => (event) => {
    if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
      return;
    }

    setState({ ...state, left: open });
  };

  const list = (anchor) => (
    <div

      role="presentation"
      onClick={toggleDrawer(anchor, false)}
      onKeyDown={toggleDrawer(anchor, false)}
    >
            <div style={{ display: "flex", alignItems: "center", justifyContent:'center',padding:8,paddingLeft:0}}>
                <Avatar src="/logo192.png" />
            </div>
      <List>
              {TabRouteList.map(route=><ListItem button
       key={route.name} 
       component={Link} 
       to={route.path} 
       label={route.ruName} >
           
            <ListItemText primary={route.ruName} />
          </ListItem>
         )}
      </List>

    </div>
  );

  return (
    <div>
      {['left'].map((anchor) => (
        <React.Fragment key={anchor}>
          <IconButton onClick={toggleDrawer(anchor, true)}>
            <MenuIcon />
          </IconButton>
          <Drawer anchor={anchor} open={state[anchor]} onClose={toggleDrawer(anchor, false)}>
            {list(anchor)}
          </Drawer>
        </React.Fragment>
      ))}
    </div>
  );
}



export default inject('authStore')(NavBar)